import React from 'react'
import logo from '../../static/logo-1024.jpg'
import "./index.css";

const IndexPage = () => (
  <div>
    <img
      src={logo}
      style={{
        margin: '0 auto',
        display: 'flex',
        width: '400',
      }}
    />
  </div>
);

export default IndexPage



